@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* PREVENT DOUBLE CLICK HEIGHTLIGHT */
  -webkit-user-select: none; /* Chrome/Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+ */
}
htm {
  font-size: 16px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: #0e0f16;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 10px;
  padding-left: 5px;
  height: 75px;
}
.brand-word {
  color: #fafafb;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-family: "Righteous", cursive;
  margin-left: 20px;
}
@media (max-width: 600px) {
  .brand-word {
    margin-left: 5px;
  }
}
@media (max-width: 375px) {
  .brand-word {
    margin-left: 0px;
    font-size: 1.2rem;
  }
}

.search-bar {
  width: 65%;
  min-width: 200px;
  max-width: 768px;
  position: relative;
}
.search-input {
  /* ALLOW INPUT HEIGHT TO CHANGE */
  -webkit-appearance: none;
  font-size: 0.9rem;
  height: 35px;
  width: 100%;
  border-radius: 5px;
  border: 2px solid #fafbfc;
  padding-left: 3px;
  padding-right: 70px;
  background-color: rgb(211, 211, 222);
}
.search-input:focus {
  outline: none;
  border: 2px solid aquamarine;
  /* box-shadow: none|h-offset v-offset blur spread color |inset|initial|inherit; */
  box-shadow: 0px 0px 4px black;
}
.search-input::-webkit-input-placeholder {
  color: #616161;
  text-indent: 16px;
}
.search-input:-ms-input-placeholder {
  color: #616161;
  text-indent: 16px;
}
.search-input::-ms-input-placeholder {
  color: #616161;
  text-indent: 16px;
}
.search-input::placeholder {
  color: #616161;
  text-indent: 16px;
}
.search-button {
  background-color: #0e0f16;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border: 2px solid #f9f9f9;
  border-left: none;
  color: aquamarine;
  font-size: 20px;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 60px;
  outline: none;
  cursor: pointer;
}
.search-button:hover {
  background-color: rgba(4, 4, 31, 0.961);
  color: white;
}

.user-icon {
  color: aquamarine;
  font-size: 2.2rem;
  cursor: pointer;
  padding-top: 0px;
  padding-bottom: 0px;
  /* PREVENT MOBILE BUTTON HEIGHLIGHT COLOR ON CLICK */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
@media (max-width: 375px) {
  .user-icon {
    font-size: 1.9rem;
  }
}
.entire-dropdown {
  background-color: white;
  position: absolute;
  right: 10px;
  top: 75px;
  width: 15vw;
  min-width: 200px;
  max-width: 210px;
}
.side-options {
  list-style: none;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 30vh;
  max-height: 300px;
  min-height: 200px;
}
.side-item {
  background-color: #0e0f16;
  padding-top: 15px;
  padding-bottom: 15px;
  height: 20%;
  color: white;
  cursor: pointer;
  text-align: center;
  border-radius: 2px;
}
.remove-drop {
  display: none;
}

.nav-item {
  background-color: rgba(238, 130, 238, 0.386);
  color: white;
  text-decoration: none;
  /* TRANSITION TIME */
  transition: 0.3s;
  margin-bottom: 45px;
  width: 70%;
  /* PREVENT WIGGLE MOTION */
  min-width: 200px;
  text-align: center;
  padding: 10px;
  border-radius: 40px;
  font-size: 1.2rem;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
}
.nav-item-icon {
  color: turquoise;
  /* font-size: 1.6rem; */
}

.side-nav {
  position: fixed;
  height: 100%;
  width: 0px;
  max-width: 0px;
  overflow-x: hidden;
  top: 75px;
  right: 0;
  background-color: rgb(8, 8, 14);
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-top: 75px;
  align-items: center;
  /* TRANSITION TIME */
  transition: 0.5s;
}

.open-close-nav {
  width: 65vw;
  max-width: 250px;
}

.lower-button {
  text-decoration: none;
  outline: none;
  cursor: pointer;
  border: none;
  background: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  /* PREVENT MOBILE BUTTON HEIGHLIGHT COLOR ON CLICK */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transition: hover 0.8s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.lower-button:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}
/* PREVENT MOBILE CLICK TO BEHAVE AS HOVER EVENT */
@media (hover: none) {
  .lower-button:hover {
    -webkit-transform: none;
            transform: none;
  }
}

.lower-button-click {
  border-radius: 500px;
  -webkit-animation-name: mak;
          animation-name: mak;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
}
@-webkit-keyframes mak {
  0% {
    background-color: white;
  }
  100% {
    background: none;
  }
}
@keyframes mak {
  0% {
    background-color: white;
  }
  100% {
    background: none;
  }
}
.button-text {
  color: #fafafb;
  font-family: "Abel", sans-serif;
  text-transform: uppercase;
  font-size: 0.7rem;
}
.button-icon {
  color: aquamarine;
  font-size: 1.8rem;
}

.right-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  max-width: 500px;
}
.mobile-user {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
/* 0px ---> infinity */
@media (min-width: 0px) {
  .right-menu {
    display: none;
  }
}

/* 769 ---------> infinity */
/* hide it from 0 to 900 */
@media (min-width: 1300px) {
  .right-menu {
    display: inherit;
  }
  .mobile-user {
    display: none;
  }
}
