.form-input {
  width: 90%;
  border-radius: 5px;
  outline: none;
  border: 1px solid #fafbfc;
  height: 5vh;
  max-height: 40px;
  min-height: 38px;
  /* box-shadow: none|h-offset v-offset blur spread color |inset|initial|inherit; */
  box-shadow: 0px 0px 15px #616161;
  padding-left: 4px;
  font-size: 1.1rem;
  background-color: rgb(211, 211, 222);
}
.form-input:focus {
  outline: none;
  border: 2px solid aquamarine;
  /* box-shadow: none|h-offset v-offset blur spread color |inset|initial|inherit; */
  box-shadow: 0px 0px 4px black;
}
.form-input::-webkit-input-placeholder {
  color: #616161;
  text-indent: 12px;
}
.form-input:-ms-input-placeholder {
  color: #616161;
  text-indent: 12px;
}
.form-input::-ms-input-placeholder {
  color: #616161;
  text-indent: 12px;
}
.form-input::placeholder {
  color: #616161;
  text-indent: 12px;
}

.login-form {
  display: flex;
  justify-content: center;
  padding-top: 6vh;
  height: 100%;
  background-color: #000109;
}
.form-content {
  background-color: #000109;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 30vh;
  min-height: 240px;
  width: 100vw;
  max-width: 560px;
  border-radius: 5px;
}

.form-button {
  background-color: #0e0f16;
  outline: none;
  border: 2px solid white;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
  cursor: pointer;
}
.form-button:hover {
  background-color: rgba(4, 4, 31, 0.961);
}
@media (hover: none) {
  .form-button:hover {
    background: none;
  }
}

